import { cn } from "@/lib/utils";
import { format } from "date-fns";
import { CalendarIcon } from "lucide-react";
import { FieldErrors, FieldValues, Path, SetFieldValue } from "react-hook-form";
import { Button } from "../button";
import { Calendar } from "../calendar";
import { Label } from "../label";
import { Popover, PopoverContent, PopoverTrigger } from "../popover";

interface CustomRHFDateInputProps<T extends FieldValues> {
  label: string;
  id: Path<T>;
  errors: FieldErrors<T>;
  setValue: SetFieldValue<T>;
  dateValue?: Date;
  handleDateChange: ({ field, date }: { field: string; date: Date }) => void;
}

const CustomRHFDateInput = <T extends FieldValues>({
  errors,
  label,
  id,
  setValue,
  dateValue,
  handleDateChange,
}: CustomRHFDateInputProps<T>) => {
  const error = errors[id];

  return (
    <div className="space-y-2">
      <Label htmlFor={id}>{label}</Label>
      <Popover>
        <PopoverTrigger asChild>
          <Button
            variant="outline"
            className={cn(
              "w-full justify-start text-left font-normal",
              error && "border-destructive"
            )}
          >
            <CalendarIcon className="mr-2 h-4 w-4" />
            {dateValue ? format(dateValue, "PPP") : "Pick a date"}
          </Button>
        </PopoverTrigger>
        <PopoverContent className="w-auto p-0">
          <Calendar
            mode="single"
            selected={dateValue}
            defaultMonth={dateValue}
            onSelect={(date) => {
              if (!date) return;
              handleDateChange({ field: id, date });
              setValue(id, date, {});
            }}
          />
        </PopoverContent>
      </Popover>
      {error && (
        <p className="text-sm text-destructive">
          {(error as { message?: string }).message}
        </p>
      )}
    </div>
  );
};

export default CustomRHFDateInput;
