/**
 * Convert a UTC date string to Bangladesh time (UTC+6).
 *
 * @param utcDateTime - An ISO UTC date string, e.g. "2025-09-29T18:00:00Z"
 * @returns A Date object in BD local time
 */
export function toBangladeshTime(utcDateTime?: Date): Date | undefined {
  if (!utcDateTime) return undefined;
  const utcDate = new Date(utcDateTime);
  // Get milliseconds and add +6 hours offset
  const bdTime = new Date(utcDate.getTime() + 6 * 60 * 60 * 1000);
  return bdTime;
}
