/**
 * StatusOption type definition
 * Represents a status option with a display name and numeric value
 */
type StatusOption = {
  /** Display name of the status option */
  name: string;
  /** Numeric value of the status option */
  value: number;
};

/**
 * getStatusName - Utility function to find status name by value
 *
 * Searches through an array of status options to find the matching status name
 * based on the provided numeric value. This is commonly used for converting
 * enum values or status codes to their human-readable display names.
 *
 * @template T - The type of status options array (extends StatusOption[])
 *
 * @param options - Array of status options to search through
 * @param statusValue - The numeric value to search for
 *
 * @returns The display name of the matching status option, or undefined if not found
 *
 * @example
 * ```typescript
 * const statusOptions = [
 *   { name: "Active", value: 1 },
 *   { name: "Inactive", value: 2 },
 *   { name: "Pending", value: 3 }
 * ];
 *
 * const statusName = getStatusName(statusOptions, 2);
 * console.log(statusName); // Output: "Inactive"
 *
 * const notFound = getStatusName(statusOptions, 99);
 * console.log(notFound); // Output: undefined
 * ```
 *
 * @example
 * ```typescript
 * // Usage with enum-like data
 * const userStatuses = [
 *   { name: "Pending Approval", value: 0 },
 *   { name: "Active", value: 1 },
 *   { name: "Suspended", value: 2 },
 *   { name: "Deactivated", value: 3 }
 * ];
 *
 * // Get status name for user with status value 1
 * const displayStatus = getStatusName(userStatuses, user.status);
 * ```
 */
export const getStatusName = (
  options: StatusOption[] | undefined,
  statusValue: number
): string | undefined => {
  if (!options) return;
  const found = options.find((option) => option.value === statusValue);
  return found?.name;
};

export default getStatusName;
