import { ReactNode } from "react";
import { FieldErrors, FieldValues, Path, SetFieldValue } from "react-hook-form";
import { Label } from "../label";
import { Select, SelectContent, SelectTrigger, SelectValue } from "../select";

interface CustomRHFSelectProps<T extends FieldValues> {
  label: string;
  id: Path<T>;
  placeholder?: string;
  setValue: SetFieldValue<T>;
  errors: FieldErrors<T>;
  children: ReactNode;
  defaultValue?: string;
  valueAsNumber?: boolean;
}

const CustomRHFSelect = <T extends FieldValues>({
  errors,
  label,
  id,
  setValue,
  placeholder,
  defaultValue,
  children,
  valueAsNumber = false,
}: CustomRHFSelectProps<T>) => {
  const error = errors[id];

  return (
    <div className="space-y-2">
      <Label htmlFor={id}>{label}</Label>
      <Select
        defaultValue={defaultValue}
        onValueChange={(value) =>
          setValue(id, valueAsNumber ? Number(value) : value, {
            // shouldValidate: true,
          })
        }
      >
        <SelectTrigger
          className={error ? "border-destructive w-full" : "w-full"}
        >
          <SelectValue
            placeholder={placeholder ? placeholder : `Select ${label}`}
          />
        </SelectTrigger>
        <SelectContent>{children}</SelectContent>
      </Select>
      {error && (
        <p className="text-sm text-destructive">
          {(error as { message?: string }).message}
        </p>
      )}
    </div>
  );
};

export default CustomRHFSelect;
